.arrow {
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-image: url("products_arrow-x1.ff35ed23.png");
  border-style: none;
  border-radius: 50%;
}

.arrow:focus, .arrow:hover {
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transform: rotate(-90deg);
  box-shadow: 2px 2px 5px #0000001f, 1px 1px 5px #00000024, 1px 2px 50px #0003;
}

.products__item:hover .products__name, .products__item:focus .products__name {
  transform: scale(1.05);
}

.products__item--pink:hover, .products__item--pink:focus {
  transform: color;
  background-color: #fcaec0;
}

.products__item--coffee:hover, .products__item--coffee:focus {
  transform: color;
  background-color: #f4c98ef0;
}

.products__item--green:hover, .products__item--green:focus {
  transform: color;
  background-color: #b6d48e;
}

.products__item:hover .products__description, .products__item:focus .products__description {
  transform: scale(1.1);
}

